import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Header from "./Header";
import "bootstrap/dist/css/bootstrap.min.css";
import "../css/LoginTest.css";
import LoginAxios from "../token/tokenAxios";
import Footer from "./Footer";

function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [userData, setUserData] = useState("");
  const navigate = useNavigate();

  const handleLogin = async () => {
    try {
      const response = await LoginAxios.post("/users/signin", {
        email,
        password,
      });

      const token = response.data.token;

      localStorage.setItem("token", token);
      console.log("Login successful. Token:", token);
      checkLoginStatus();
      navigate("/"); //   
    } catch (error) {
      if (error.response && error.response.status === 401) {
        // 401 Unauthorized     
        //       .
        // setUserMessage(" :   .");
      } else {
        //    
      }
    }
  };
  const checkLoginStatus = async () => {
    try {
      //        .
      const response = await LoginAxios.get("/userinfo");

      //     .
      const user = response.data;

      //    .
      setUserData(user);

      console.log("User info retrieved successfully:", user);
    } catch (error) {
      //           
      if (error.response && error.response.status === 401) {
        //       .
        // setUserMessage(" :   .");
      } else {
        //    
      }
    }
  };

  return (
    <div>
      <Header />
      <div className="wrap">
        <div className="login">
          <form
            className="LoginForm"
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <h1 className="title">  S.With .</h1>
            <br />

            <div className="login_id">
              <h4 className="s_text">Email</h4>
              <label className=""></label>
              <input
                type="text"
                name="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder=" "
              />
            </div>

            <div className="login_pw">
              <h4 className="s_text">Password</h4>
              <label className=""></label>
              <input
                type="password"
                name="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder=" "
              />
            </div>
            <div className="loginButton">
              <button
                type="button"
                name="login"
                onClick={handleLogin}
                className="btn round"
                style={{
                  backgroundColor: "#75ddff",
                  width: "150px",
                  height: "50px",
                  margin: "20px",
                  borderRadius: "30px",
                  fontFamily: "SUITE-Regular",
                  fontSize: "18px",
                }}
              >
                login
              </button>
            </div>
            <div className="login_sns">
              <button
                type="button"
                name="login"
                onClick={handleLogin}
                className="btn round"
                style={{
                  backgroundColor: "#ffffb5",
                  width: "350px",
                  height: "50px",
                  margin: "10px",
                  marginTop: "20px",
                  borderRadius: "30px",
                  fontFamily: "SUITE-Regular",
                  fontSize: "18px",
                }}
              >
                 
              </button>

              <button
                type="button"
                name="login"
                onClick={handleLogin}
                className="btn round"
                style={{
                  backgroundColor: "#ffffb5",
                  width: "350px",
                  height: "50px",
                  margin: "10px",
                  borderRadius: "30px",
                  fontFamily: "SUITE-Regular",
                  fontSize: "18px",
                }}
              >
                Github 
              </button>
            </div>
          </form>
          <br></br>
          <div className="loginButton">
            <li>
              <a href="/register">
                <button
                  type="button"
                  className="btn round"
                  style={{
                    backgroundColor: "#ffb9e4",
                    width: "350px",
                    height: "50px",
                    margin: "10px",
                    borderRadius: "30px",
                    fontFamily: "SUITE-Regular",
                    fontSize: "18px",
                  }}
                >
                  S.With 
                </button>
              </a>
            </li>
            <li>
              <a href="/find">
                <button
                  type="button"
                  className="btn round"
                  style={{
                    backgroundColor: "#ffb9e4",
                    width: "350px",
                    height: "50px",
                    margin: "10px",
                    borderRadius: "30px",
                    fontFamily: "SUITE-Regular",
                    fontSize: "18px",
                  }}
                >
                  S.With / 
                </button>
              </a>
            </li>
          </div>
        </div>
      </div>
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <Footer />
    </div>
  );
}

export default Login;
