import React, { useState, useEffect } from "react";
import usersUserinfoAxios from "../token/tokenAxios";
import Header from "./Header";
import axios from "axios";
import sample6_execDaumPostcode from "./KakaoAddress";
import { useNavigate } from "react-router-dom";
import { isTokenAvailable } from "../token/tokenAxios";
import Footer from "./Footer";

const UpdateUser = () => {
  //

  const [confirmNickname, setConfirmNickname] = useState(""); //nickname 
  const [confirmPassword, setConfirmPassword] = useState("");
  const [isButtonDisabled, setIsButtonDisabled] = useState(false);
  const navigate = useNavigate();

  const [userData, setNewUser] = useState({
    email: "",
    password: "",
    nickname: "",
    user_profile: "",
    img: "",
    useraddress: "",
    user_introduction: "",
  });

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        //     
        const response = await usersUserinfoAxios.get("/users/userinfo");
        setNewUser(response.data); //       
        console.log(userData);
      } catch (error) {
        console.error("Failed to fetch user data.", error);
      }
    };

    fetchUserData();
  }, []);

  //profile update
  const handleUpdateProfile = async (e) => {
    e.preventDefault();

    try {
      //   
      const response = await usersUserinfoAxios.post(
        "http://localhost:8080/users/updateUserProfile",
        userData,
        //   
        {
          withCredentials: true,
        }
      );
      // profile       userData 
      setNewUser(response.data);
      setNewUser((prevUser) => [...prevUser, response.data]);
      window.location.reload();
      console.log("   ");
    } catch (error) {
      console.error(" ", error);
    }
  };
  //profile img

  const handleImageChange = (e) => {
    const files = e.target.files;
    // Check if any file is selected
    if (files.length > 0) {
      const file = files[0];
      const reader = new FileReader();
      reader.onloadend = () => {
        setNewUser((prevUser) => ({ ...prevUser, img: reader.result }));
      };
      reader.readAsDataURL(file);
    } else {
      // Handle the case when no file is selected
      setNewUser((prevUser) => ({ ...prevUser, img: null }));
    }
  };
  useEffect(() => {}, [userData.img]);

  const handleInputChange = (e) => {
    //e   target
    const { name, value } = e.target;

    setNewUser((prevUser) => ({ ...prevUser, [name]: value }));
  };

  const handleUpdateUser = async (e) => {
    e.preventDefault();

    try {
      //   
      const response = await usersUserinfoAxios.post(
        "http://localhost:8080/users/updateUser",
        userData,
        //   

        {
          withCredentials: true,
        }
      );
      //       userData 
      setNewUser(response.data);
      setNewUser((prevUser) => [...prevUser, response.data]);
      window.location.reload();
      console.log("   ");
    } catch (error) {
      console.error(" ", error);
    }
  };

  //     
  const handleNickname = async (e) => {
    e.preventDefault();
    const { nickname } = userData;
    const maxLength = 10;
    if (nickname.length > maxLength) {
      alert(` ${maxLength}  .`);
      return;
    }
    try {
      const response = await axios.post(
        "http://localhost:8080/users/nickname",
        userData,
        {
          withCredentials: true,
        }
      );

      setConfirmNickname(response.data.toString());
      if (response.data !== "existsNick") {
        alert("  .");
        setConfirmNickname("new");
      } else if (response.data === "existsNick") {
        alert("  .");
        setConfirmNickname("existsNick");
      }
    } catch (error) {
      console.error(" .", error);
    }
  };
  //password
  const handleInputChange1 = (e) => {
    //e   target
    const { name, value } = e.target;

    setNewUser((prevUser) => ({ ...prevUser, [name]: value }));
  };

  //password update button
  const handleUpdatePassword = async (e) => {
    e.preventDefault();
    if (isButtonDisabled === true && userData.password === confirmPassword) {
      try {
        //   
        const response = await usersUserinfoAxios.post(
          "http://localhost:8080/users/updatePassword",
          userData, //   
          {
            withCredentials: true,
          }
        );
        //       userData 
        console.log("   ");
        alert("  .");
      } catch (error) {
        console.error(" ", error);
      }
    } else {
      alert("  ");
    }
  };
  //password constraint
  const handleConfirmPassword = async (e) => {
    console.log("swithUser.password", userData.password);
    console.log("confirmPassword", confirmPassword);
    const passwordRegex =
      /^(?=.*?[a-z])(?=.*?[0-9])(?=.*?[~?!@#$%^&*_-]).{8,}$/;
    if (userData.password === confirmPassword) {
      // Check if the password meets the regex pattern
      if (passwordRegex.test(confirmPassword)) {
        alert("   .");
        setIsButtonDisabled(true);
      } else {
        alert("    .");
      }
    } else {
      alert("  .");
    }
  };
  const handlePasswordChange = (e) => {
    const { value } = e.target;
    setConfirmPassword(value);
  };

  //delete
  const handleDelete = async (e) => {
    e.preventDefault();
    const isConfirmedDelete = window.confirm("?");
    if (isConfirmedDelete) {
      try {
        //   
        const response = await usersUserinfoAxios.post(
          "http://localhost:8080/users/deleteUser",
          userData,
          //  
          {
            withCredentials: true,
          }
        );
        const responseLikes = await usersUserinfoAxios.post(
          "http://localhost:8080/users/deleteLikes",
          userData,
          {
            withCredentials: true,
          }
        );
        const responseApplication = await usersUserinfoAxios.post(
          "http://localhost:8080/users/deleteApplication",
          userData,
          {
            withCredentials: true,
          }
        );
        console.log("  "); // ,
        if (isTokenAvailable() !== null) {
          //  
          localStorage.removeItem("token");
          navigate("/");
          window.location.reload();
        } else {
          //     
          console.error(" ");
        }
      } catch (error) {
        console.error(" ", error);
      }
    }
  };

  return (
    <div>
      <Header />
      <div>
        <h4
          className="title"
          style={{
            marginBottom: "100px",
            overflow: "hiddlen",
            whiteSpace: "nowrap",
          }}
        >
           
        </h4>
        <div>
          <div className="register_id m-3">
            <div className="two">
              <h4
                className="s_text"
                style={{
                  marginLeft: "40px",
                  overflow: "hiddlen",
                  whiteSpace: "nowrap",
                }}
              >
                 (profile image)
              </h4>
            </div>
            <label className="m-2"></label>
            <input
              className="image_input"
              type="file"
              accept="image/*" //      
              name="img"
              onChange={(e) => handleImageChange(e)}
              style={{
                marginRight: "10px",
                overflow: "hiddlen",
                whiteSpace: "nowrap",
              }}
            />
            {/*       */}
            <div className="profile-image-container">
              {userData.img === null ? (
                <img
                  src={userData.img}
                  style={{
                    width: "200px",
                    height: "200px",
                    margin: "10px",
                    marginTop: "20px",
                    marginBottom: "10px",
                    borderRadius: "30px",
                  }}
                  alt=" "
                  className="profile-image"
                />
              ) : (
                <img
                  src={`data:image/jpeg;base64,${userData.user_profile}`}
                  style={{
                    width: "200px",
                    height: "200px",
                    margin: "10px",
                    marginTop: "20px",
                    marginBottom: "10px",
                    borderRadius: "30px",
                  }}
                  alt=" "
                  className="profile-image"
                />
              )}
            </div>
            <br />
            <button
              onClick={handleUpdateProfile}
              type="button"
              name="login"
              className="btn round"
              style={{
                fontFamily: "NPSfontBold",
                fontSize: "18px",
                backgroundColor: "#75ddff",
                width: "200px",
                height: "50px",
                margin: "10px",
                marginTop: "20px",
                marginBottom: "10px",
                borderRadius: "30px",
              }}
            >
               
            </button>
          </div>
        </div>
      </div>
      <br />
      <br />
      <br />
      <br />
      <h4
        className="title"
        style={{
          marginBottom: "50px",
          overflow: "hiddlen",
          whiteSpace: "nowrap",
        }}
      >
          
      </h4>
      <br />
      <div className="two">
        <h4
          className="s_text"
          style={{
            marginLeft: "40px",
            overflow: "hiddlen",
            whiteSpace: "nowrap",
          }}
        >
          (nick name)
        </h4>
      </div>

      <div>
        <input
          type="text"
          name="nickname"
          value={userData.nickname}
          onChange={handleInputChange}
          style={{
            marginLeft: "170px",
            overflow: "hiddlen",
            whiteSpace: "nowrap",
          }}
        />
        <button
          onClick={handleNickname}
          className="btn round"
          style={{
            fontFamily: "SUITE-Regular",
            fontSize: "18px",
            backgroundColor: "#ffffb5",
            width: "150px",
            height: "50px",
            margin: "10px",
            marginTop: "5px",
            borderRadius: "30px",
          }}
        >
           
        </button>
      </div>

      <div className="two">
        <h4 className="s_text" style={{ marginLeft: "40px" }}>
          (address)
        </h4>
      </div>
      <div>
        <input
          type="text"
          id="useraddress"
          style={{
            marginLeft: "170px",
            overflow: "hiddlen",
            whiteSpace: "nowrap",
          }}
        />
        <input
          name="useraddress"
          className="btn round"
          style={{
            fontFamily: "SUITE-Regular",
            fontSize: "18px",
            backgroundColor: "#ffffb5",
            width: "150px",
            height: "50px",
            margin: "10px",
            marginTop: "5px",
            borderRadius: "30px",
            marginLeft: "10px",
          }}
          type="button"
          value=" "
          onClick={() => sample6_execDaumPostcode({ setNewUser })}
        />
      </div>
      <div className="two">
        <h4
          className="s_text"
          style={{
            marginLeft: "40px",
            overflow: "hiddlen",
            whiteSpace: "nowrap",
          }}
        >
          (self introduction)
        </h4>
      </div>
      <div>
        <input
          type="text"
          name="user_introduction"
          value={userData.user_introduction}
          onChange={handleInputChange}
          style={{ marginRight: "0px" }}
        />
      </div>
      <button
        onClick={handleUpdateUser}
        type="button"
        name="login"
        className="btn round"
        style={{
          fontFamily: "SUITE-Regular",
          fontSize: "18px",
          backgroundColor: "#75ddff",
          width: "200px",
          height: "50px",
          margin: "10px",
          marginTop: "20px",
          marginBottom: "10px",
          borderRadius: "30px",
        }}
      >
        
      </button>
      <br />
      <br />
      <br />
      <br />
      <div>
        <h4
          className="title"
          style={{
            marginBottom: "50px",
            overflow: "hiddlen",
            whiteSpace: "nowrap",
          }}
        >
           {" "}
        </h4>
        <div className="two">
          <h4
            className="s_text"
            style={{
              marginLeft: "1%",
              overflow: "hiddlen",
              whiteSpace: "nowrap",
              fontFamily: "SUITE-Regular",
              fontSize: "18px",
            }}
          >
            ,,  8  
          </h4>
        </div>
        <br />
        <input
          className="textInput"
          type="password"
          name="password"
          value={userData.password}
          autoComplete="off"
          onChange={handleInputChange1}
        />
        <br />
        <input
          className="textInput"
          type="password"
          name="confirmPassword"
          value={confirmPassword}
          autoComplete="off"
          onChange={handlePasswordChange}
          style={{ marginLeft: "180px" }}
        />
        <button
          disabled={isButtonDisabled}
          onClick={handleConfirmPassword}
          className="btn round"
          style={{
            fontFamily: "SUITE-Regular",
            fontSize: "18px",
            backgroundColor: "#ffffb5",
            width: "165px",
            height: "50px",
            margin: "10px",
            marginTop: "5px",
            borderRadius: "30px",
          }}
        >
           
        </button>
        <br />
        <button
          onClick={handleUpdatePassword}
          type="button"
          name="login"
          className="btn round"
          style={{
            fontFamily: "SUITE-Regular",
            fontSize: "18px",
            backgroundColor: "#75ddff",
            width: "200px",
            height: "50px",
            margin: "10px",
            marginTop: "20px",
            marginBottom: "10px",
            borderRadius: "30px",
          }}
        >
           
        </button>
        <br />
        <br />
        <br />
        <br />
        <button
          onClick={handleDelete}
          className="btn round"
          style={{
            fontFamily: "SUITE-Regular",
            fontSize: "18px",
            backgroundColor: "#FFB9E4",
            width: "200px",
            height: "50px",
            margin: "10px",
            marginTop: "20px",
            marginBottom: "10px",
            borderRadius: "30px",
          }}
        >
           
        </button>
        <br />
        <br />
        <br />
      </div>
      <Footer />
    </div>
  );
};

export default UpdateUser;
