//   
import { useState, useEffect } from "react";
import usersUserinfoAxios from "../token/tokenAxios";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import Header from "./Header";
import "../css/FindUser.css";
import Footer from "./Footer";

function FindUser() {
  const [number, setNumber] = useState(""); // 
  const [confirm, setConfirm] = useState(""); // input
  const [confirmEmail, setConfirmEmail] = useState(false); //email input 
  const [isButtonDisabled, setIsButtonDisabled] = useState(false);
  const [isButtonDisabled2, setIsButtonDisabled2] = useState(false);
  const [isMessageVisible, setIsMessageVisible] = useState(false); //email  
  const [isPasswordVisible, setIsPasswordVisible] = useState(false); //password 
  const [confirmPassword, setConfirmPassword] = useState("");
  const [swithUser, setSwithUser] = useState({
    email: "",
    username: "",
    password: "",
  }); //email
  const [email, setEmail] = useState("");
  const navigate = useNavigate();

  const handleInputChange = (event) => {
    setEmail(event.target.value);
    setIsMessageVisible(false);
  };
  const handleInputChange2 = (e) => {
    //e   target
    const { name, value } = e.target;
    setSwithUser((prevUser) => ({ ...prevUser, [name]: value }));
  };
  const [userData, setUserData] = useState("");

  const handleFind = async (e) => {
    e.preventDefault();

    //    
    if (!email.trim()) {
      setIsMessageVisible(false);
      setConfirmEmail("none");
      return; //     
    }

    try {
      const response = await axios.post(
        "http://localhost:8080/users/ExistEmail",
        { email },
        {
          withCredentials: true,
        }
      );
      const responseData = response.data.toString();
      // responseData  ConfirmEmail 
      if (responseData === "existsEmail") {
        setConfirmEmail("existsEmail");
      } else {
        setConfirmEmail("none");
      }
      //      
      setIsMessageVisible(true);
    } catch (error) {
      console.error(" .", error);
      setIsMessageVisible(false);
      setConfirmEmail("none");
    }
  };

  // handleEmail   
  const handleEmail = async (e) => {
    e.preventDefault();
    console.log(swithUser.email + (swithUser.email ? " email" : ""));
    if (handleInputChange2 === null) {
      alert(" ");
    } else {
      try {
        const response = await axios.post(
          "http://localhost:8080/users/findPassword",
          swithUser,
          {
            withCredentials: true,
          }
        );

        setConfirm(response.data.toString());
        alert(" .");
        console.log(response.data.toString() + "");
        console.log(response.data);
      } catch (error) {
        console.error(" .", error);
      }
    }
  };

  const handleNumberChange = (e) => {
    const { value } = e.target;
    setNumber(value);
  };

  const handleConfirm = async () => {
    console.log("number:", number);
    console.log("confirm:", confirm);
    if (
      number === null ||
      confirm === null ||
      number === "" ||
      confirm === ""
    ) {
      alert(" ");
      console.error(" ");
    } else if (number === confirm) {
      alert(" .");
      setIsButtonDisabled(true);
      setIsPasswordVisible(true);
      //    a 
    } else {
      alert("  .");
      console.error(" ");
    }
  };
  const handleInputChange1 = (e) => {
    //e   target
    const { name, value } = e.target;

    setSwithUser((prevUser) => ({ ...prevUser, [name]: value }));
  };
  //password update button
  const handleUpdatePassword = async (e) => {
    e.preventDefault();
    if (isButtonDisabled2 === true && swithUser.password === confirmPassword) {
      try {
        //   
        const response = await usersUserinfoAxios.post(
          "http://localhost:8080/users/updatePassword",
          swithUser, //   
          {
            withCredentials: true,
          }
        );
        //       userData 
        console.log("   ");
        alert("  .");
      } catch (error) {
        console.error(" ", error);
      }
    } else {
      alert("  ");
    }
  };
  //password constraint
  const handleConfirmPassword = async (e) => {
    console.log("swithUser.password", swithUser.password);
    console.log("confirmPassword", confirmPassword);
    const passwordRegex =
      /^(?=.*?[a-z])(?=.*?[0-9])(?=.*?[~?!@#$%^&*_-]).{8,}$/;
    if (swithUser.password === confirmPassword) {
      // Check if the password meets the regex pattern
      if (passwordRegex.test(confirmPassword)) {
        alert("   .");
        setIsButtonDisabled2(true);
      } else {
        alert("    .");
      }
    } else {
      alert("  .");
    }
  };
  const handlePasswordChange = (e) => {
    const { value } = e.target;
    setConfirmPassword(value);
  };

  return (
    <div>
      <Header />

      <br></br>
      <div className="finduser_box">
        <div>
          <div className="finduser_title">(email) </div>

          <div className="finduser_textbox">
            <div
              className="finduser_text"
              style={{ fontFamily: "SUITE-Regular", fontSize: "18px" }}
            >
               Email 
            </div>
          </div>
          <div className="finduser_input_box">
            <input
              className="finduser_textInput"
              type="text"
              name="email"
              onChange={handleInputChange}
              required
            />
            <button
              onClick={handleFind}
              className="btn round"
              style={{
                backgroundColor: "#ffffb5",
                width: "100px",
                height: "50px",
                borderRadius: "30px",
                marginLeft: "8px",
                fontFamily: "SUITE-Regular",
                fontSize: "18px",
              }}
            >
              
            </button>
          </div>
          <p className={isMessageVisible ? "visible" : "hidden"}>
            {confirmEmail === "existsEmail"
              ? `${email}   .`
              : `${email}    .`}
          </p>
        </div>
      </div>

      <div className="finduser_box">
        <div className="finduser_emailtitle"> </div>

        <div className="finduser_emailbox">
          <div
            className="finduser_emailtext"
            style={{ fontFamily: "SUITE-Regular", fontSize: "18px" }}
          >
             
          </div>
        </div>
        <div>
          <input
            className="finduser_emailInput"
            type="text"
            name="email"
            onChange={handleInputChange2}
            required
          />
          <button
            onClick={handleEmail}
            className="btn round"
            style={{
              backgroundColor: "#ffffb5",
              width: "100px",
              height: "50px",
              marginLeft: "8px",
              borderRadius: "30px",
              fontFamily: "SUITE-Regular",
              fontSize: "18px",
            }}
          >
            
          </button>
        </div>

        <div>
          <input
            type="text"
            name="number"
            className="textInput_check"
            value={number}
            onChange={handleNumberChange}
          />
          <button
            disabled={isButtonDisabled}
            onClick={handleConfirm}
            className="btn round"
            style={{
              backgroundColor: "#ffffb5",
              width: "100px",
              height: "50px",
              margin: "10px",
              marginTop: "5px",
              borderRadius: "30px",
              fontFamily: "SUITE-Regular",
              fontSize: "18px",
            }}
          >
            
          </button>
        </div>
        <br />
      </div>

      <br />
      <br />
      <br />

      <div className="finduser_box">
        <div className={isPasswordVisible ? "visible" : "hidden"}>
          <h4>  </h4>
          <a style={{ fontFamily: "SUITE-Regular", fontSize: "18px" }}>
            ,,  8  
          </a>
          <br />
          <input
            className="finduser_emailInput2"
            type="password"
            name="password"
            value={userData.password}
            autoComplete="off"
            onChange={handleInputChange1}
          />
          <br />
          <input
            className="finduser_textInput"
            type="password"
            name="confirmPassword"
            value={confirmPassword}
            autoComplete="off"
            onChange={handlePasswordChange}
          />
          <button
            disabled={isButtonDisabled2}
            onClick={handleConfirmPassword}
            className="btn round"
            style={{
              backgroundColor: "#ffffb5",
              width: "170px",
              height: "50px",
              margin: "10px",
              marginTop: "5px",
              borderRadius: "30px",
              fontFamily: "SUITE-Regular",
              fontSize: "18px",
            }}
          >
             
          </button>
          <br />
          <button
            onClick={handleUpdatePassword}
            type="button"
            name="login"
            className="btn round"
            style={{
              backgroundColor: "#75ddff",
              width: "200px",
              height: "50px",
              margin: "10px",
              marginTop: "20px",
              marginBottom: "10px",
              borderRadius: "30px",
              fontFamily: "SUITE-Regular",
              fontSize: "18px",
            }}
          >
             
          </button>
        </div>
      </div>
      <Footer />
    </div>
  );
}
export default FindUser;
