/* eslint-disable class-methods-use-this */
import SockJS from "sockjs-client";
import Stomp from "stompjs";
import { useState } from "react";
import { messageService } from "../Service/MessageService";
import usersUserinfoAxios from "../../../../token/tokenAxios";
//const baseUrl = "http://localhost:8080";
const baseUrl = "http://lemonadswith.store:8080";

export default class MessageStore {
  constructor(nickname) {
    this.listeners = new Set();

    this.nickname = nickname;

    this.socket = null;
    this.client = null;
    this.connected = false;

    this.currentRoomIndex = 0;
    this.messageEntered = "";

    this.messageLogs = [];
  }

  setNicknameId = async () => {
    try {
      const response = await usersUserinfoAxios.get("/users/userinfo");
      const NickName = response.data.nickname;
      this.nickname = NickName;
      this.publish(); //  user_no 
    } catch (error) {
      console.error("     :", error);
    }
  };

  connect(post_no) {
    return new Promise((resolve, reject) => {
      console.log("WebSocket   ...");
      this.socket = new SockJS(`${baseUrl}/chat`);
      this.client = Stomp.over(this.socket);

      this.currentRoomIndex = post_no;

      this.client.connect(
        {},
        () => {
          console.log("WebSocket  .");
          this.connected = true;
          this.publish();
          resolve(); //   resolve 
        },
        (error) => {
          console.error("WebSocket  .", error);
          reject(error); //    reject 
        }
      );
    });
  }

  async connectAndSubscribe(post_no, nickname) {
    try {
      this.setNicknameId(nickname); //     userId .
      await this.connect(post_no);

      this.subscribeMessageBroker(post_no);
      //     
      const response = await fetch(
        `http://lemonadswith.store:8080/studyRoom/post/${post_no}`
      );
      const responseData = await response.json();
      this.updateMessageLogs(responseData); //   
    } catch (error) {
      console.error("     :", error);
    }
  }

  subscribeMessageBroker(post_no, nickname) {
    //  
    this.client.connect({}, () => {
      this.setNicknameId(nickname);
      this.client.subscribe(
        `/subscription/chat/room/${post_no}`,
        (messageReceived) => this.receiveMessage(messageReceived),
        {}
      );

      this.sendMessage({ type: "enter" });
    });
  }

  disconnect = () => {
    console.log("WebSocket  ...");
    if (this.connected) {
      //     
      this.sendMessage({ type: "quit" });
      this.client.unsubscribe();
      this.client.disconnect();

      this.connected = false;
      this.currentRoomIndex = 0;
      this.messageEntered = "";
      this.messageLogs = [];
      this.publish();
    } else {
      console.warn(
        "WebSocket    .   ."
      );
    }
  };
  updateMessageLogs = (newMessageLogs) => {
    this.messageLogs = newMessageLogs;
    this.publish();
  };
  changeInput(value) {
    this.messageEntered = value;
    this.publish();
  }

  sendMessage({ type }) {
    console.log("sendMessage called, connected:", this.connected);

    if (this.connected) {
      const message = type === "message" ? this.messageEntered : "";

      messageService.sendMessage({
        client: this.client,
        messageToSend: {
          type,
          post_no: this.currentRoomIndex,
          nickname: this.nickname,
          message,
        },
      });
      console.log(this.nickname);
      this.messageEntered = "";
      this.publish();
    } else {
      console.error(
        "   .    ."
      );
    }

    this.messageEntered = "";
    this.publish();
  }

  receiveMessage(messageReceived) {
    //       
    const message = JSON.parse(messageReceived.body);
    console.log("Received message:", message);
    this.messageLogs = [...this.messageLogs, this.formatMessage(message)];
    this.publish();
  }

  formatMessage(message) {
    return {
      message_id: message.message_id,
      value: `${message.value} (${new Date().toLocaleTimeString()})`,
    };
  }

  subscribe(listener) {
    this.listeners.add(listener);
  }

  unsubscribe(listener) {
    this.listeners.delete(listener);
  }

  publish() {
    this.listeners.forEach((listener) => listener());
  }
}

export const messageStore = new MessageStore();
