import React, { useState, useEffect } from "react";
import "../css/NewBoard.css";
import FormFour from "./FormFour";
import usersUserinfoAxios from "../token/tokenAxios";
import axios from "axios";
import Modal from "react-modal";
import DeleteIcon from "./img/delete.png";
import Footer from "./Footer";

const StudyProject = ({ handleDataFromChild }) => {
  // formData   

  const customStyles = {
    content: {
      width: "50%", //    .
      height: "50%", //    .
      margin: "auto", //    .
    },
  };
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [keyword, setKeyword] = useState("");
  const [cafes, setCafes] = useState([]);

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const handleButtonClick = () => {
    if (!isModalOpen) {
      openModal();
    } else {
      closeModal();
    }
  };

  const handleSearch = async () => {
    try {
      const response = await usersUserinfoAxios.get(
        `/KeywordCafes?keyword=${keyword}`
      );
      setCafes(response.data);
    } catch (error) {
      console.error("Error searching cafes:", error);
    }
  };
  const handleItemClick = (cafe) => {
    setKeyword(cafe.bplcnm);
    setStudy_place(cafe.sitewhladdr);
    closeModal();
  };
  const [applicationCount, setApplicationCount] = useState();

  const [duration, setDuration] = useState();
  const [techStack, setTechStack] = useState([]);
  const [deadline, setDeadline] = useState("");
  const [region, setRegion] = useState("");
  const [study_place, setStudy_place] = useState("");

  const [studyMethod, setStudyMethod] = useState("");

  const [studyTitle, setStudyTitle] = useState("");
  const [studyContent, setStudyContent] = useState("");

  const [startDate, setStartDate] = useState("");

  const handleFormSubmit = (e) => {
    e.preventDefault();
  };

  const [selectedItem2, setSelectedItem2] = useState(null);

  const handleItem2Click = (item2) => {
    if (selectedItem2 === item2) {
      //        
      setSelectedItem2(null);
    } else {
      //    
      setSelectedItem2(item2);
    }

    // setStudyMethod    handleDataChange  
    setStudyMethod(item2);
    handleDataChange();
  };

  const handleTechStackChange = (e) => {
    const selectedTech = e.target.value;

    //     
    if (techStack.includes(selectedTech)) {
      //   ,    
      setTechStack(techStack.filter((tech) => tech !== selectedTech));
    } else if (techStack.length < 5) {
      //   5   
      setTechStack([...techStack, selectedTech]);
    } else {
      // 5    
      alert(" 5  .");
    }
    console.log(": " + techStack);
  };

  const handleDeleteTech = (deletedTech) => {
    setTechStack(techStack.filter((tech) => tech !== deletedTech));
  };

  const handleDataChange = () => {
    // techStack    
    const techStackAsNumbers = techStack.map((tech) => parseInt(tech));
    //       
    handleDataFromChild({
      studyTitle,
      studyContent,
      studyMethod,
      applicationCount,
      duration,
      techStack: techStackAsNumbers,
      deadline,
      region,
      study_place,
      startDate,
    });
    console.log(": " + techStack);
  };

  //      
  const techStackOptions = {
    1: "Angular",
    2: "C",
    3: "C++",
    4: "Django",
    5: "Docker",
    6: "Express",
    7: "Figma",
    8: "Firebase",
    9: "Flask",
    10: "Flutter",
    11: "Git",
    12: "Go",
    13: "GraphQL",
    14: "Java Script",
    15: "Java",
    16: "Kotlin",
    17: "Kubernetes",
    18: "MongoDB",
    19: "mySql",
    20: "NestJS",
    21: "NextJS",
    22: "NodeJS",
    23: "Php",
    24: "Python",
    25: "R",
    26: "React",
    27: "React Native",
    28: "Spring",
    29: "Svelte",
    30: "Swift",
    31: "Type Script",
    32: "Unity",
    33: "Vue",
    34: "Zeplin",
  };

  console.log("js: " + studyMethod);

  return (
    <div>
      <div className="post_1">
        <span className="post_1_title">2</span>
        <h2 className="post_title">S.With   .</h2>
      </div>
      <ul className="postToggle_ul">
        <li
          className={`postToggle ${
            selectedItem2 === "" ? "clicked" : ""
          }`}
          onClick={() => {
            handleItem2Click("");
            setStudyMethod("");
          }}
        >
          <span className="postToggle_text"></span>
        </li>
        <li
          className={`postToggle ${
            selectedItem2 === "" ? "clicked" : ""
          }`}
          onClick={() => {
            handleItem2Click("");
            setStudyMethod("");
          }}
        >
          <span className="postToggle_text"></span>
        </li>
        <li
          className={`postToggle ${
            selectedItem2 === "/" ? "clicked" : ""
          }`}
          onClick={() => {
            handleItem2Click("/");
            setStudyMethod("/");
          }}
        >
          <span className="postToggle_text">/</span>
        </li>
      </ul>
      <br />
      <br />
      <div className="post_1">
        <span className="post_1_title">3</span>
        <h2 className="post_title">S.With   .</h2>
      </div>
      <form onSubmit={handleFormSubmit} className="all_form">
        <div className="all_form_div">
          <label className="post_3_label">
             :
            <input
              type="number"
              value={applicationCount}
              onChange={(e) => setApplicationCount(e.target.value)}
              onBlur={handleDataChange}
            />
          </label>

          <label className="post_3_label">
             :
            <input
              type="number"
              placeholder=" "
              value={duration}
              onChange={(e) => setDuration(e.target.value)}
              onBlur={handleDataChange}
            />
          </label>

          <div className="post_4_label">
             :
            <select
              value={techStack}
              onChange={handleTechStackChange}
              onBlur={handleDataChange}
            >
              <option value="0" style={{ display: "none" }}></option>
              {Object.entries(techStackOptions).map(([value, label]) => (
                <option key={value} value={value}>
                  {label}
                </option>
              ))}
            </select>
          </div>
          <div className="post_4_label">
             :
            <input
              type="date"
              value={startDate}
              onChange={(e) => setStartDate(e.target.value)}
              onBlur={handleDataChange}
            />
          </div>

          <label className="stack_box">
            <div className="selected">
              <br />
              {techStack.map((stack, index) => (
                <div className="selected_2" key={index}>
                  <div className="tech-stack-item">
                    {techStackOptions[stack]}
                  </div>

                  <img
                    src={DeleteIcon}
                    className="delete_img"
                    alt="deleteButton"
                    onClick={() => handleDeleteTech(stack)}
                  />
                </div>
              ))}
            </div>
          </label>

          <label className="post_3_label">
             :
            <input
              type="date"
              value={deadline}
              onChange={(e) => setDeadline(e.target.value)}
              onBlur={handleDataChange}
            />
          </label>
          <label className="post_3_label">
             :
            <select
              value={region}
              onChange={(e) => setRegion(e.target.value)}
              onBlur={handleDataChange}
            >
              <option value="0" style={{ display: "none" }}></option>
              <option value="//">//</option>
              <option value="//">//</option>
              <option value="//">//</option>
              <option value="//">//</option>
              <option value="///">
                ///
              </option>
              <option value="//">//</option>
              <option value="/">/</option>
              <option value="///">
                ///
              </option>
              <option value=""></option>
              <option value="//">//</option>
              <option value="//">//</option>
              <option value="///">///</option>
              <option value=""></option>
            </select>
          </label>
          {selectedItem2 === "" || selectedItem2 === "/" ? (
            <label className="post_3_label">
               :
              <div className="cafeModal">
                <input
                  type="text"
                  value={keyword}
                  onChange={(e) => setKeyword(e.target.value)}
                  className="cafeSearchInput"
                  placeholder="  ."
                  onBlur={handleDataChange}
                />
                <button
                  onClick={() => {
                    handleButtonClick();
                    handleSearch();
                  }}
                >
                  Search
                </button>
                <div>
                  <Modal
                    isOpen={isModalOpen}
                    onRequestClose={closeModal}
                    style={customStyles}
                  >
                    <button
                      onClick={() => {
                        closeModal();
                      }}
                      id="modalCloseBtn"
                    >
                      
                    </button>
                    <ul>
                      {cafes.map((cafe) => (
                        <li
                          key={cafe.id}
                          onClick={() => handleItemClick(cafe)}
                          className="cafe_p"
                        >
                          <p className="cafe_p">
                            {cafe.bplcnm} ( {cafe.sitewhladdr} )
                          </p>
                        </li>
                      ))}
                    </ul>
                  </Modal>
                </div>
              </div>
            </label>
          ) : null}
        </div>
      </form>
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <div>
        <div className="post_1">
          <span className="post_1_title">4</span>
          <h2 className="post_title">S.With  .</h2>
        </div>
        <div className="studyProject_title">
          <label htmlFor="title" className="titleLabel">
             :
            <input
              type="text"
              id="title"
              name="title"
              placeholder=" ."
              value={studyTitle}
              onChange={(e) => setStudyTitle(e.target.value)}
              onBlur={handleDataChange}
            />
          </label>
          <br />
          <div className="studyProject_content">
            <label htmlFor="content" className="contentLabel">
              <textarea
                type="text"
                id="content"
                name="content"
                placeholder=" ."
                value={studyContent}
                onChange={(e) => setStudyContent(e.target.value)}
                onBlur={handleDataChange}
              />
            </label>
          </div>
        </div>
      </div>
    </div>
  );
};

export default StudyProject;
