import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import Header from "./Header";
import "../css/RegisterUser.css";
import Required from "./img/required.png";
import sample6_execDaumPostcode from "./KakaoAddress";
import girl from "../main/img/girl.png";
import Footer from "./Footer";
import AxiosUser from "../token/tokenAxios"
function RegisterUser() {
  const [number, setNumber] = useState(""); // 
  const [confirmPassword, setConfirmPassword] = useState("");

  const navigate = useNavigate();

  const [data, setData] = useState([]);
  const [confirm, setConfirm] = useState(""); // input
  const [confirmNickname, setConfirmNickname] = useState("");
  const [isVisible, setIsVisible] = useState(false);
  const [isButtonDisabled, setIsButtonDisabled] = useState(false);
  const [isButtonDisabled1, setIsButtonDisabled1] = useState(false);
  const [swithUser, setNewUser] = useState({
    email: "",
    password: "",
    username: "",
    nickname: "",
    img: "",
    useraddress: "",
    user_introduction: "",
    role: "",
  });

  //
  const [allAgree, setAllAgree] = useState(false);
  const [Agreements, setAgreements] = useState({
    all: false,
    terms: false,
    personalInfo: false,
    provision: false,
    location: false,
  });

  const handleAgreementChange = (e) => {
    //  
    const { name, checked } = e.target;
    setAgreements((prevAgreements) => ({ ...prevAgreements, [name]: checked }));

    const allChecked = Object.values({ ...Agreements, [name]: checked }).every(
      (value) => value === true
    );

    if (allChecked) {
      setAllAgree(true);
    }
  };

  const handleAllAgreementChange = (e) => {
    const { checked } = e.target;
    setAgreements((prevAgreements) =>
      Object.keys(prevAgreements).reduce((newAgreements, AgreementKey) => ({
        ...newAgreements,
        [AgreementKey]: checked,
      }))
    );
    setAllAgree(checked);
  };

  const handleToggle = () => {
    setIsVisible(!isVisible);
  };

  const handleInputChange = (e) => {
    //e   target
    const { name, value } = e.target;
    setNewUser((prevUser) => ({ ...prevUser, [name]: value }));
  };

  const handleEmail = async (e) => {
    e.preventDefault();
    try {
      const response = await AxiosUser.post(
        "/users/mail",
        swithUser,
        {
          withCredentials: true,
        }
      );

      setConfirm(response.data.toString());
      //db   alert   . else  .
      if (response.data !== "exists") {
        console.log(response.data);
        console.log(" :", response);
        console.log("ok");
        alert(" .()");
      } else {
        alert("  ");

        console.log(response.data);
      }
    } catch (error) {
      console.error(" .", error);
    }
  };

  //     
  const handleNickname = async (e) => {
    e.preventDefault();
    const { nickname } = swithUser;
    const maxLength = 10;
    if (nickname.length > maxLength || nickname.length === 0) {
      alert(` ${maxLength} , 0  .`);
      return;
    }
    try {
      const response = await AxiosUser.post(
        "/users/nickname",
        swithUser,
        {
          withCredentials: true,
        }
      );

      setConfirmNickname(response.data.toString());
      console.log(response.data + " ?");
      if (response.data !== "existsNick") {
        alert("  .");
        setConfirmNickname("new");
      } else if (response.data === "existsNick") {
        alert("  .");
        setConfirmNickname("existsNick");
      }
    } catch (error) {
      console.error(" .", error);
    }
  };

  //email
  const handleNumberChange = (e) => {
    const { value } = e.target;
    setNumber(value);
  };
  const handleConfirm = async () => {
    console.log("number:", number);
    console.log("confirm:", confirm);
    if (number === confirm) {
      alert(" ,  .");
      setIsButtonDisabled(true);
      //    a 
    } else {
      alert("  .");
      console.error(" ");
    }
  };

  //password
  const handleConfirmPassword = async (e) => {
    console.log("swithUser.password", swithUser.password);
    console.log("confirmPassword", confirmPassword);
    const passwordRegex =
      /^(?=.*?[a-z])(?=.*?[0-9])(?=.*?[~?!@#$%^&*_-]).{8,}$/;
    if (swithUser.password === confirmPassword) {
      // Check if the password meets the regex pattern
      if (passwordRegex.test(confirmPassword)) {
        alert("   .");
        setIsButtonDisabled1(true);
      } else {
        alert("    .");
      }
    } else {
      alert("  .");
    }
  };
  const handlePasswordChange = (e) => {
    const { value } = e.target;
    setConfirmPassword(value);
  };

  const handleAddUser = async () => {
    if (
      isButtonDisabled === true &&
      swithUser.password === confirmPassword &&
      confirmNickname === "new" &&
      allAgree === true
    ) {
      try {
        //   

        const response = await AxiosUser.post(
          "/users/register",
          swithUser,
          {
            withCredentials: true,
          }
        );
        //address
        const address = document.getElementById("useraddress").value;
        setNewUser((prevUser) => ({ ...prevUser, useraddress: address }));
        setData((prevUser) => [...prevUser, response.data]);
        console.log(confirmNickname);
        alert(" .");
        navigate("/login");
      } catch (error) {
        console.error(" .", error);
      }
    } else if (
      isButtonDisabled === false &&
      swithUser.password !== confirmPassword
    ) {
      alert("  ");
    } else if (
      isButtonDisabled === true &&
      swithUser.password !== confirmPassword
    ) {
      alert(" . ");
    } else {
      alert("  ");
      console.log(confirmNickname);
    }
  };
  //profile
  const handleImageChange = (e) => {
    const file = e.target.files[0]; //  
    const reader = new FileReader();

    reader.onloadend = () => {
      setNewUser((prevUser) => ({ ...prevUser, img: reader.result }));
    };

    if (file) {
      reader.readAsDataURL(file);
    }
  };

  return (
    <div>
      <Header />

      <br></br>
      <h1 className="title"></h1>
      <br></br>
      <h3 className="subTitle"> S.With </h3>

      <div className="container_register">
        <form className="m-5 mb-1">
          <div className="register_id ml-5">
            <div className="two">
              <h4 className="register_s_text_id">
                (email)
                <img src={Required} className="required_img" />
              </h4>
            </div>

            <div style={{ position: "relative" }}>
              <input
                className="textInput"
                type="text"
                name="email"
                value={swithUser.email}
                onChange={handleInputChange}
                required
              />

              <button
                onClick={handleEmail}
                className="btn round"
                style={{
                  backgroundColor: "#ffffb5",
                  width: "170px",
                  height: "50px",
                  margin: "10px",
                  marginTop: "5px",
                  borderRadius: "30px",
                  position: "absolute",
                  fontFamily: "SUITE-Regular",
                  fontSize: "18px",
                }}
              >
                 
              </button>
            </div>
            <br />
            <div style={{ position: "relative" }}>
              <input
                type="text"
                name="number"
                className="textInput"
                value={number}
                onChange={handleNumberChange}
              />
              <button
                disabled={isButtonDisabled}
                onClick={handleConfirm}
                className="btn round"
                style={{
                  backgroundColor: "#ffffb5",
                  width: "170px",
                  height: "50px",
                  margin: "10px",
                  marginTop: "5px",
                  borderRadius: "30px",
                  position: "absolute",
                  fontFamily: "SUITE-Regular",
                  fontSize: "18px",
                }}
              >
                 
              </button>
            </div>
          </div>
          <div className="register_id m-3">
            <div className="two">
              <h4 className="s_text">
                (password)
                <img src={Required} className="required_img" />
              </h4>
              <p className="register_password_massage">
                ,,  8  
              </p>
            </div>

            <input
              className="textInput"
              type="password"
              name="password"
              value={swithUser.password}
              autoComplete="off"
              onChange={handleInputChange}
              required
            />
            <br />
            <div style={{ position: "relative" }}>
              <input
                className="textInput"
                type="password"
                name="confirmPassword"
                value={confirmPassword}
                autoComplete="off"
                onChange={handlePasswordChange}
                placeholder="   ."
              />

              <button
                disabled={isButtonDisabled1}
                onClick={handleConfirmPassword}
                className="btn round"
                style={{
                  backgroundColor: "#ffffb5",
                  width: "170px",
                  height: "50px",
                  margin: "10px",
                  marginTop: "5px",
                  borderRadius: "30px",
                  position: "absolute",
                  fontFamily: "SUITE-Regular",
                  fontSize: "18px",
                }}
              >
                 
              </button>
            </div>
          </div>

          <div className="register_id m-3">
            <div className="two">
              <h4 className="s_text">
                (name)
                <img src={Required} className="required_img" />
              </h4>
            </div>
            <label className="m-2"></label>
            <input
              className="textInput"
              type="text"
              name="username"
              value={swithUser.username}
              onChange={handleInputChange}
              required
            />
          </div>
          <div className="register_id m-3">
            <div className="two">
              <h4 className="s_text">
                (nick name)
                <img src={Required} className="required_img" />
              </h4>
            </div>
            <div style={{ position: "relative" }}>
              <input
                className="textInput"
                type="text"
                name="nickname"
                value={swithUser.nickname}
                onChange={handleInputChange}
                required
              />

              <button
                onClick={handleNickname}
                className="btn round"
                style={{
                  backgroundColor: "#ffffb5",
                  width: "170px",
                  height: "50px",
                  margin: "10px",
                  marginTop: "5px",
                  borderRadius: "30px",
                  position: "absolute",
                  fontFamily: "SUITE-Regular",
                  fontSize: "18px",
                }}
              >
                 
              </button>
            </div>
          </div>
          <div className="register_id m-3">
            <div className="two">
              <h4 className="s_text"> (profile image)</h4>
            </div>
            <label className="m-2"></label>
            <input
              className="textInput"
              style={{ paddingTop: "7px" }}
              type="file"
              accept="image/*" //      
              name="img"
              onChange={(e) => handleImageChange(e)}
            />
            {/*       */}
            <div className="profile-image-container">
              {swithUser.img && (
                <img
                  src={swithUser.img}
                  alt=" "
                  className="profile-image"
                />
              )}
            </div>
          </div>
          <div className="register_id m-3">
            <div className="two">
              <h4 className="s_text">
                (address)
                <img src={Required} className="required_img" />
              </h4>
            </div>

            <div style={{ position: "relative" }}>
              <input type="text" id="useraddress" className="textInput" />

              <input
                name="useraddress"
                className="btn round"
                style={{
                  backgroundColor: "#ffffb5",
                  width: "150px",
                  height: "50px",
                  margin: "10px",
                  marginTop: "5px",
                  borderRadius: "30px",
                  position: "absolute",
                  fontFamily: "SUITE-Regular",
                  fontSize: "18px",
                }}
                type="button"
                value=" "
                onClick={() => sample6_execDaumPostcode({ setNewUser })}
                required
              />
            </div>
          </div>

          <div className="register_id m-3">
            <div className="two">
              <h4 className="s_text">(self introduction)</h4>
            </div>
            <label className="m-2"></label>
            <textarea
              className="textInput_intro"
              type="text"
              name="user_introduction"
              value={swithUser.user_introduction}
              onChange={handleInputChange}
            />
          </div>

          <div>
            <input
              type="text"
              name="role"
              value={swithUser.role}
              onChange={handleInputChange}
              hidden={true}
            />
          </div>
          <br />
          <section className="agreement_section ">
            <div className={isVisible ? "visible" : "visible"}>
              <ul>
                <li className="agreement_allcheck_li">
                  <label htmlFor="agreement_allcheck"> </label>
                  <input
                    className="agreement_checkbox"
                    type="checkbox"
                    id="agreement_allcheck"
                    name="agreement_allcheck"
                    checked={allAgree}
                    onChange={handleAllAgreementChange}
                  />
                </li>
                <li>
                  <label htmlFor="agreement_terms">   []</label>
                  <input
                    className="agreement_checkbox"
                    type="checkbox"
                    id="agreement_terms"
                    name="terms"
                    required
                    checked={Agreements.terms}
                    onChange={handleAgreementChange}
                  />
                </li>
                <li>
                  <label htmlFor="agreement_personalInfo">
                        []
                  </label>
                  <input
                    className="agreement_checkbox"
                    type="checkbox"
                    id="agreement_personalInfo"
                    name="personalInfo"
                    required
                    checked={Agreements.personalInfo}
                    onChange={handleAgreementChange}
                  />
                </li>
                <li>
                  <label htmlFor="agreement_provision">
                      3   []
                  </label>
                  <input
                    className="agreement_checkbox"
                    type="checkbox"
                    id="agreement_provision"
                    name="provision"
                    required
                    checked={Agreements.provision}
                    onChange={handleAgreementChange}
                  />
                </li>
                <li>
                  <label htmlFor="agreement_location">
                       []
                  </label>
                  <input
                    className="agreement_checkbox"
                    type="checkbox"
                    id="agreement_location"
                    name="location"
                    required
                    checked={Agreements.location}
                    onChange={handleAgreementChange}
                  />
                </li>
              </ul>
            </div>
          </section>
          <br />
          <button
            onClick={handleAddUser}
            type="button"
            name="login"
            className="btn round"
            style={{
              backgroundColor: "#75ddff",
              width: "200px",
              height: "50px",
              margin: "10px",
              marginTop: "20px",
              marginBottom: "10px",
              borderRadius: "30px",
              fontFamily: "SUITE-Regular",
              fontSize: "18px",
            }}
          >
             
          </button>
        </form>
        <div className="login_sns">
          <button
            type="button"
            name="login"
            className="btn round"
            style={{
              backgroundColor: "#ffffb5",
              width: "150px",
              height: "50px",
              margin: "10px",
              marginTop: "20px",
              borderRadius: "30px",
              fontFamily: "SUITE-Regular",
              fontSize: "18px",
            }}
          >
             
          </button>

          <button
            type="button"
            name="login"
            className="btn round"
            style={{
              backgroundColor: "#ffffb5",
              width: "150px",
              height: "50px",
              margin: "10px",
              borderRadius: "30px",
              fontFamily: "SUITE-Regular",
              fontSize: "18px",
            }}
          >
            Github 
          </button>
        </div>
      </div>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <Footer />
    </div>
  );
}
export default RegisterUser;
